// import React, { Component } from "react";
import Carousel from "react-bootstrap/Carousel";
import MyImagesFluid from "./MyImagesFluid.js";

function MyCarousel(props) {
  const imagesList = props.imagesList;
  return (
    <Carousel fade className="w-100 overflow-hidden position-relative mb-0">
      {imagesList.map((image, idx) => (
        <Carousel.Item key={idx}>
          <MyImagesFluid src={image.path} alt={image.alt} />
          <Carousel.Caption className="position-absolute top-50 ">
            <h3>First slide label</h3>
            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
          </Carousel.Caption>
        </Carousel.Item>
      ))}
    </Carousel>
  );
}

export default MyCarousel;
